<template>
  <div class="submitOrder">
    <van-nav-bar title="提交订单" left-arrow @click-left="back" />
    <div class="order-box">
      <div class="selectAdress">
        <h3 @click="showPopup()">
          <van-icon name="location-o" />选择收货地址
          <van-icon name="arrow" />
        </h3>
        <div class="adressInfo">
          <div class="name">
            {{selectCurrentAddress.name}}
            <span>{{selectCurrentAddress.tel}}</span>
          </div>
          <div class="detail">{{selectCurrentAddress.address}}</div>
        </div>
      </div>
      <div class="orderMessage">
        <h3>订单信息</h3>

        <div class="orderInfo">
          <div class="menu-box" v-for="(item,index) in orderData" :key="index">
            <div class="menu-list clearfix">
              <div class="menu-img fl">
                <img class="auto-img" :src="item.small_img" alt />
              </div>
              <div class="menu-content fl clearfix">
                <div class="menu-name">
                  {{item.name}}
                  <span class="spec">{{item.rule}}</span>
                </div>
                <div class="menu-enname">{{item.enname}}</div>
                <div class="menu-count">x{{item.count}}</div>
                <span class="menu-pri">￥{{item.price}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="orderTotal">
        <div class="orderTotal-bor clearfix">
          <div class="proToltal fl">共计{{calculationData.count}}件商品</div>
          <div class="toltal fr">
            合计
            <span>￥{{calculationData.total}}</span>
          </div>
        </div>
        <div class="left-box left"></div>
        <div class="left-box right"></div>
      </div>
      <van-submit-bar :price="calculationData.total*100" button-text="立即结算" @submit="pay" />
    </div>
    <van-popup v-model="show" position="bottom" round>
      <div v-if="list.length>0">
        <van-address-list
          v-model="chosenAddressId"
          :list="list"
          default-tag-text="默认"
          @add="goNewlyAddress()"
          @select="selectAdress"
        />
      </div>
      <div v-else>
        <van-empty description="您还没有任何收货地址">
          <van-button round type="default" class="bottom-button" @click="goNewlyAddress">前往添加</van-button>
        </van-empty>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/submitOrder.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      value: "",
      show: false,
      chosenAddressId: "", //选择地址id
      list: [], //地址列表数据
      sids: [], //订单id
      orderData: [], //订单数据
      calculationData: {
        //计算数据
        count: 0,
        total: 0,
      },
      //选择地址数据
      selectCurrentAddress: {
        name: "",
        tel: "",
        address: "",
      },
    };
  },
  created() {
    this.sids = this.$route.query.sids.split("-");
    this.findAddress();
    this.findOrder();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },

    showPopup() {
      this.show = true;
    },
    //新建地址
    goNewlyAddress() {
      this.$router.push({ name: "NewlyAddress" });
    },
    //查找地址
    findAddress() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 20000) {
            result.data.result.map((v) => {
              let address = v.province + v.city + v.county + v.addressDetail;
              this.list.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address,
                isDefault: Boolean(v.isDefault),
              });
              if (Boolean(v.isDefault)) {
                this.chosenAddressId = v.aid;
                this.selectCurrentAddress.name = v.name;
                this.selectCurrentAddress.tel = v.tel;
                this.selectCurrentAddress.address = address;
              }
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //查找订单
    findOrder() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
          sids: JSON.stringify(this.sids),
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 50000) {
            if (result.data.result.length === 0) {
              return this.$router.push({ name: "Menu" });
            }
            result.data.result.map((v) => {
              this.calculationData.count += v.count;
              this.calculationData.total += v.price * v.count;
            });
            this.orderData = result.data.result;
          }
        })
        .catch((err) => {});
    },
    //选择地址
    selectAdress(item, index) {
      for (let key in this.selectCurrentAddress) {
        this.selectCurrentAddress[key] = item[key];
      }
      this.show = false;
    },
    //结算下单
    pay() {
      if (this.chosenAddressId == "") {
        this.$toast("请选择收货地址");
        return;
      }
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
        return;
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(this.sids),
        phone: this.selectCurrentAddress.tel,
        address: this.selectCurrentAddress.address,
        receiver: this.selectCurrentAddress.name,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/pay",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          this.$toast(result.data.msg);
          if (result.data.code == 60000) {
            setTimeout(() => {
              this.$router.push({ name: "Order" });
            }, 800);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>